(function ($) {
	"use strict";

	$(document).ready(function () {
		initAnimation();
		initOwlCarousel();
		initSlick();
		initFancybox();
		handleNavMobile();
		changeProductLayout();
		selectRating();
		handleQuickview();
		handleScrollTop();
		handleCountdown();
		formShowPassword();
		previewAvatar();
        priceRange();
		showMenuMobile();
		activeMenuCustomer();
		accountAddressAction();
	});
	function initAnimation() {
		new WOW().init();
	}
	function initOwlCarousel() {
		if ($(".owl-carousel").length) {
			$(".owl-carousel:not(.custom-carousel)").each(function () {
				var owl = $(".owl-carousel");
				$(this).owlCarousel({
					margin: 0,
					autoplayTimeout: $(this).data("autotime"),
					smartSpeed: $(this).data("speed"),
					autoHeight: $(this).data("autoheight"),
					autoplay: $(this).data("autoplay"),
					items: $(this).data("carousel-items"),
					nav: $(this).data("nav"),
					dots: $(this).data("dots"),
					center: $(this).data("center"),
					loop: $(this).data("loop"),
					animateOut: $(this).data("animate") ? "fadeOut" : false,
					autoplayHoverPause: true,
					responsive: {
						0: {
							items: $(this).data("mobile"),
							margin: $(this).data("marginmb")
								? $(this).data("marginmb")
								: $(this).data("margintb"),
						},
						768: {
							items: $(this).data("tablet"),
							margin: $(this).data("margintb"),
						},
						992: {
							items: $(this).data("desktop-small"),
							margin: $(this).data("margintb"),
						},
						1680: {
							items: $(this).data("desktop"),
							margin: $(this).data("margintb"),
						},
					},
				});
			});
		}
	}
	function initSlick() {
		if ($(".news-slider").length) {
			$(".news-slider").slick({
				speed: 600,
				dots: true,
				slidesToShow: 2,
				variableWidth: true,
			});
		}
	}
	function initFancybox() {
		if ($("#mainCarousel").length) {
			const mainCarousel = new Carousel(
				document.querySelector("#mainCarousel"),
				{
					Dots: false,
					on: {
						createSlide: (carousel, slide) => {},
						deleteSlide: (carousel, slide) => {
							if (slide.Panzoom) {
								slide.Panzoom.destroy();
								slide.Panzoom = null;
							}
						},
					},
				}
			);

			const thumbCarousel = new Carousel(
				document.querySelector("#thumbCarousel"),
				{
					Sync: {
						target: mainCarousel,
						friction: 0,
					},
					Dots: false,
					Navigation: false,
					center: true,
					infinite: false,
				}
			);
		}

		if ($("#mainCarousel1").length) {
			const mainCarousel = new Carousel(
				document.querySelector("#mainCarousel1"),
				{
					Dots: false,
					on: {
						createSlide: (carousel, slide) => {},
						deleteSlide: (carousel, slide) => {
							if (slide.Panzoom) {
								slide.Panzoom.destroy();
								slide.Panzoom = null;
							}
						},
					},
				}
			);

			const thumbCarousel = new Carousel(
				document.querySelector("#thumbCarousel1"),
				{
					Sync: {
						target: mainCarousel,
						friction: 0,
					},
					Dots: false,
					Navigation: false,
					center: true,
					infinite: false,
				}
			);
		}
	}
	function handleNavMobile() {
		$(".mobile-menu-list .caret").click(function () {
			$(this).next().toggleClass("show");
		});
	}
	function changeProductLayout() {
		$(".layout-view a").click(function () {
			let layout = $(this).data("href");
			$(".layout-view a").removeClass("active");
			$(this).addClass("active");

			$(".product-wrap").removeClass("grid list");
			$(".product-wrap").addClass(layout);
		});
	}
	function selectRating() {
		$(".select-rate .ic-star").click(function () {
			let value = $(this).data("value");
			$(".select-rate .ic-star").removeClass("active");
			$("#rate_value").val(value);

			for (let index = 1; index <= value; index++) {
				$(`.select-rate .ic-star[data-value='${index}']`).addClass(
					"active"
				);
			}
		});
	}
	function handleQuickview() {
		$(".quick-view").click(function () {
			$(".quickview-backdrop").addClass("show");
		});
		$(".close-quickview").click(function () {
			$(this).closest(".quickview-backdrop").removeClass("show");
		});
	}
	function handleScrollTop() {
		$(".go-top").click(function () {
			$("html, body").animate({ scrollTop: 0 }, "slow");
			return false;
		});
	}
	function handleCountdown() {
		$(".countdown-timer").each(function () {
			let data = $(this).data("date");
			$(this).countdown(data, function (e) {
				$(this).find(".dates .text").text(e.strftime("%D"));
				$(this).find(".hours .text").text(e.strftime("%H"));
				$(this).find(".minutes .text").text(e.strftime("%M"));
				$(this).find(".seconds .text").text(e.strftime("%S"));
			});
		});
	}
	function formShowPassword() {
        $(".password-field .show-password").click(function () {
            let icon = $(this).children();
            if (icon.hasClass("ic-eye-slashed")) {
                icon.removeClass("ic-eye-slashed").addClass("ic-eye-opened");
                $(this).prev().attr("type", "text");
            } else {
                icon.addClass("ic-eye-slashed").removeClass("ic-eye-opened");
                $(this).prev().attr("type", "password");
            }
        });
    }
	function previewAvatar() {
        $(".avatar-input").on("change", function () {
            let input = this;
            if (input.files && input.files[0]) {
                var reader = new FileReader();

                reader.onload = function (e) {
                    $(".avatar-preview img").attr("src", e.target.result);
                };

                reader.readAsDataURL(input.files[0]);
            }
        });
    }

	function priceRange() {
        if ($("#slider-range").length) {
            $("#slider-range").slider({
                range: true,
                min: 0,
                max: 1000000,
                values: [$("#price_from").val(), $("#price_to").val()],
                slide: function (event, ui) {
                    $("#price_from").val(ui.values[0]);
                    $("#price_to").val(ui.values[1]);
					$('.from').html(ui.values[0]);
					$('.to').html(ui.values[1]);
                },
            });
            $("#amount").val(
                "$" +
                    $("#slider-range").slider("values", 0) +
                    " - $" +
                    $("#slider-range").slider("values", 1)
            );
        }
    }

	function showMenuMobile(){
		$('.filter-icon-mobile').on('click', function(){
			$('.product-filter').toggleClass('show');
		})
		$('.close-sidebar').on('click', function(){
			$('.product-filter').removeClass('show');
		})
	}

	function activeMenuCustomer(){
		var anchor = $('a.sidebar_link');
		var current = location.href;
		console.log(anchor);
		for (var i = 0; i < anchor.length; i++) {
			if(anchor[i].href == current) {
				anchor[i].className = "active";
			}
		}
	}
	function accountAddressAction() {
        $(".address-item .action > a").click(function () {
            $(this).toggleClass("rotate");
            $(this).next().toggle();
        });
    }
})(jQuery);
